import { useAnimation, motion } from "framer-motion";
import { useEffect, useState } from "react";

function App() {
  const [target, settarget] = useState();
  const [computer, setcomputer] = useState();
  const [userrun, setuserrun] = useState();
  const arr = [1, 2, 3, 4, 5, 6, 7, 8, 9];
  const [score, setscore] = useState(0);
  const [Update, setUpdate] = useState();
  const [out, setout] = useState();
  const [start, setstart] = useState();
  const [win, setwin] = useState();

  const FindTarget = () => {
    const Target = Math.floor(Math.random() * 100) + 1;
    if (Target !== 0 && Target >= 30) settarget(Target);
    else FindTarget();
  };

  const ComputerValue = () => {
    const value = Math.floor(Math.random() * 10) + 1;
    if (value > 0 && value <= 9) {
      setcomputer(value);
    }
  };
  const UserScore = (val) => {
    ComputerValue();
    setuserrun(val);
    setUpdate(!Update);
  };

  const animation = useAnimation();

  useEffect(() => {
    if (userrun > 0 && computer > 0) setscore((val) => val + userrun);
    if (
      userrun === computer &&
      computer !== undefined &&
      userrun !== undefined
    ) {
      setscore((val) => val - userrun);
      setout(!out);
      setTimeout(() => {
        settarget();
        setscore(0);
        setuserrun();
        setcomputer();
        settarget();
        setout(false);
        setstart(!start);
      }, 5000);
    }

    if (score + userrun >= target) {
      setwin(!win);
      console.log("Win");
      setTimeout(() => {
        settarget();
        setscore(0);
        settarget(undefined);
        setuserrun();
        setcomputer();
        setstart(!start);
        setwin(false);
      }, 5000);
    }
    if (Update) {
      animation.start({
        scale: [1, 1.3, 1],
        rotateX: [0, 90, 0],
        transition: { duration: 0.3 },
      });
    } else {
      animation.start({
        scale: [1, 1.2, 1],
        rotateX: [0, 90, 0],
        transition: { duration: 0.3 },
      });
    }
  }, [Update]);

  return (
    <div className="bg-blue-950 w-screen h-screen overflow-hidden relative">
      <div className="flex justify-around  items-center mt-7  relative flex-wrap">
        <p className="text-center  text-4xl font-bold tracking-widest uppercase bg-gradient-to-r from-purple-700 to-red-500 bg-clip-text text-transparent">
          HandCricket
        </p>
        <div className="flex gap-x-10">
          <motion.button
            className={`text-white bg-pink-400 w-fit h-fit p-2 rounded-lg font-bold text-2xl uppercase tracking-wide ${
              target >= 30 ? "animate-bounce" : ""
            } `}
          >
            Target:{target || " -"}
          </motion.button>
          <motion.button
            className={`text-white bg-red-400 w-fit h-fit p-2 rounded-lg font-bold text-2xl uppercase tracking-wide `}
            animate={animation}
          >
            Score:{score || " -"}
          </motion.button>
          <button
            className={`text-white bg-green-400 w-fit h-fit p-2 rounded-lg font-bold text-2xl uppercase tracking-wide ${
              start ? "cursor-not-allowed" : ""
            }`}
            onClick={() => {
              FindTarget();
              setstart(!start);
            }}
            disabled={start === true}
          >
            Start
          </button>
        </div>
      </div>
      {start ? (
        <div className="flex justify-around mt-20 mr-40 flex-wrap ">
          <div className="flex  flex-col ">
            <div className="ml-2">
              <p className="text-4xl font-bold tracking-widest uppercase text-yellow-500">
                Computer
              </p>
            </div>
            <div className="flex flex-col   gap-y-3 flex-wrap  h-80 mt-20  items-center">
              {arr.map((val, i) => (
                <p
                  key={i}
                  className=" h-16 text-white font-bold text-2xl flex justify-center items-center w-16 text- mx-3 border-2 border-blue-600 bg-slate-700"
                >
                  {val}
                </p>
              ))}
            </div>
          </div>
          <div className="flex    flex-col ">
            <div className="text-end">
              <p className="text-4xl font-bold tracking-widest uppercase text-rose-500">
                You
              </p>
            </div>
            {out || win ? (
              out ? (
                <p className="font-bold text-3xl text-center font-serif absolute top-[50%]  text-yellow-500 tracking-widest capitalize ">
                  You are Out
                  <br /> <span>You Lose the Match</span>
                  <br />
                  <span>Redirecting to home...</span>
                </p>
              ) : (
                <p className="font-bold text-3xl text-center font-serif absolute top-[50%]  text-yellow-500 tracking-widest capitalize ">
                  You Won
                  <br /> <span>Redirecting to home...</span>
                </p>
              )
            ) : (
              <div className="flex flex-col   gap-y-3 flex-wrap  mt-20 w-40 h-80 items-center">
                {arr.map((val, i) => (
                  <button
                    key={i}
                    className=" h-16 text-white font-bold text-2xl   w-16 text- mx-3 border  bg-[#1AACAC] rounded-lg hover:bg-teal-400 shadow-lg hover:shadow-orange-400"
                    onClick={() => UserScore(val)}
                  >
                    {val}
                  </button>
                ))}
              </div>
            )}
          </div>
        </div>
      ) : (
        <div>
          <p className="font-bold text-3xl font-serif text-center mt-40 text-yellow-500 tracking-widest capitalize">
            Please Press the Start Button
          </p>
        </div>
      )}

      {start && (
        <div className="absolute lg:top-[53%] left-[38%] max-sm:-top:[90%] flex gap-x-60">
          <p className="text-4xl font-bold tracking-widest uppercase text-yellow-500">
            {userrun && computer ? computer : "-"}
          </p>
          <p className="text-4xl font-bold tracking-widest uppercase text-rose-500">
            {userrun && computer ? userrun : "-"}
          </p>
        </div>
      )}
    </div>
  );
}

export default App;
